.navbar {
    overflow: hidden;
    background-color: /*  #edd8d1; */    #e5c7bd;     /* white;    *//* #e3e8ec */  /*   #aa9a9d ;  */
    position: fixed;
    top: 0;
    z-index: 1000; 
    -webkit-box-shadow: 0px 1px 4px 0px rgba(0,0,0,0.2);
    -moz-box-shadow: 0px 1px 4px 0px rgba(0,0,0,0.2);
    box-shadow: 0px 1px 4px 0px rgba(0,0,0,0.2);
  }
  
  .navbar a {
    position: relative;
    float: left;
    display: block;
    color: #020202;
    text-align: center;
    padding:  .7rem 2rem;
    text-decoration: none;
    font-size: 1rem;
    margin: 0px 15px;
    font-family: 'Raleway', sans-serif;
    letter-spacing: .1rem;
  }

  .navbar a:after{
    position: absolute;
    bottom: 7px;
    left: 0px;
    width: 100%;
    height: 4px;
    background-color: #ff8c9b; /* rgb(250, 241, 109);*/ 
    content: "";
    opacity: 0;
    transition: opacity 0.3s ease 0s, transform 0.3s ease 0s;
    transform: translateY(20px); 
  }
  
  .navbar a:hover:after{
    opacity: 1;
    transform: translateY(0px)
  } 
.navbar :active{
  color:#ff8c9b;
}
  .my-navbar-padding{
padding: 0
  }